@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;600;700;800;900&display=swap);
.navbar-component{background:white;padding:20px 30px;position:fixed;top:0px;right:0px;left:0px;width:100%;background:none}.navbar-component .top-logo{font-size:1.2rem;display:inline-block;font-weight:900;font-family:'Nunito Sans', 'Avenir Next', -apple-system, sans-serif}

.jumbo-component{height:50vh}.jumbo-component .intro-container{text-align:center;box-shadow:0px 0px 0px 10px #B99458;display:block;padding:10vh 20px 11vh;margin:15vh 0px}.jumbo-component .intro-container .big-title{font-size:6rem;text-transform:uppercase;font-weight:900;margin-bottom:-10px}.jumbo-component .intro-container .sub-title{text-transform:capitalize;font-size:1.8rem;font-weight:700}.jumbo-component .intro-container .sub-sub-title{padding-top:20px;font-size:1.4rem;opacity:0.5;display:inline-block;max-width:600px}@media (max-width: 760px){.jumbo-component .intro-container{padding:100px 50px;margin:150px 20px}.jumbo-component .intro-container .big-title{font-size:4rem}.jumbo-component .intro-container .sub-title{font-size:1.3rem}}

.portfolio-component .demo-component{padding:30px}.portfolio-component .demo-component .intro-col{padding-top:200px}.portfolio-component .demo-component .intro-col .project-title{font-weight:900;font-size:3rem;text-transform:uppercase}.portfolio-component .demo-component .intro-col .project-point .content{font-size:1.4rem;opacity:0.5;font-weight:700}.portfolio-component .demo-component .intro-col .action-container{padding-top:20px}.portfolio-component .demo-component .intro-col .action-container .button{padding:15px 30px;background:#B99458;color:white;font-weight:800;display:inline-block;border-radius:100px;text-transform:uppercase;letter-spacing:1px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none}.portfolio-component .demo-component .intro-col .action-container .button:hover{background:#c8ab7c;transition:all 0.1s;transform:scale(0.98)}.portfolio-component .demo-component .intro-col .action-container .button:active{background:#c8ab7c;transform:scale(0.95)}

.service-component.container{padding-bottom:100px}.service-component.container .stack-component{margin-top:50px;position:relative}.service-component.container .stack-component .col-sm-12.col-md-6.col-lg-3{padding-left:5px;padding-right:5px}.service-component.container .stack-component .item{padding:20px 20px;text-align:center;background:white;box-shadow:0 0 50px rgba(0,0,0,0.1);border-radius:20px;margin-bottom:50px;height:360px;margin-top:30px}@media (max-width: 1250px){.service-component.container .stack-component .item{height:400px}}.service-component.container .stack-component .item .info-container{transform:translateY(-30px)}.service-component.container .stack-component .item .info-container img{margin-top:5px;max-height:64px;max-width:300px}.service-component.container .stack-component .item .title{font-weight:800;font-size:1.6rem;color:rgba(0,0,0,0.8)}.service-component.container .stack-component .item .details{padding-top:10px;color:#878787;font-weight:700}.service-component.container .stack-component .item .icon-card{transform:translateY(-60px);background:white;box-shadow:0px 8px 20px -2px rgba(0,0,0,0.1);display:inline-block;padding:30px;border-radius:30px;color:white;background:#B99458}.service-component.container .stack-component .item .icon-card i.fa{font-size:2.5rem}@media (max-width: 760px){.service-component.container .stack-component .item{padding-left:30px;padding-right:30px;height:400px}.service-component.container .stack-component .item .title{font-size:2rem}.service-component.container .stack-component .item .details{font-size:1.3rem}.service-component.container .stack-component .item .info-container img{max-height:80px}}.service-component.container .stack-component .item:hover .title,.service-component.container .stack-component .item:active .title{color:#000}.service-component.container .stack-component .item:hover .details,.service-component.container .stack-component .item:active .details{color:#787878}.service-component.container .stack-component .item:hover .icon-card,.service-component.container .stack-component .item:active .icon-card{transition:.2s size}

.value-component.container{padding-bottom:100px}.value-component.container .item-component{margin-top:50px;position:relative}.value-component.container .item-component .col-xs-12{padding-left:3px;padding-right:3px}.value-component.container .item-component .item{padding:10px;text-align:center;background:white;box-shadow:0 0 50px rgba(0,0,0,0.1);border-radius:10px;margin-bottom:50px;height:200px;margin-top:30px}.value-component.container .item-component .item .info-container{transform:translateY(-30px)}.value-component.container .item-component .item .info-container img{margin-top:5px;max-height:64px;max-width:300px}.value-component.container .item-component .item .title{font-weight:800;font-size:1.2rem;color:rgba(0,0,0,0.8)}.value-component.container .item-component .item .details{font-size:0.9rem;padding-top:10px;color:#878787;font-weight:600}.value-component.container .item-component .item .icon-card{transform:translateY(-50px);background:white;box-shadow:0px 8px 20px -2px rgba(0,0,0,0.1);display:inline-block;padding:18px;height:70px;width:70px;border-radius:100px;color:white;background:#B99458}.value-component.container .item-component .item .icon-card i.fa{font-size:2rem}@media (max-width: 760px){.value-component.container .item-component .item{padding-left:30px;padding-right:30px}.value-component.container .item-component .item .title{font-size:2rem}.value-component.container .item-component .item .details{font-size:1.3rem}.value-component.container .item-component .item .info-container img{max-height:80px}}.value-component.container .item-component .item:hover .title,.value-component.container .item-component .item:active .title{color:#000}.value-component.container .item-component .item:hover .details,.value-component.container .item-component .item:active .details{color:#787878}.value-component.container .item-component .item:hover .icon-card,.value-component.container .item-component .item:active .icon-card{transition:.2s size}

.landing-component{overflow-x:hidden}.landing-component .landing-big-title{text-transform:uppercase;font-size:7rem;font-weight:900;letter-spacing:10px;padding:0px;padding-top:50px;transform:translateX(-7px)}@media (max-width: 760px){.landing-component .landing-big-title{font-size:4rem;letter-spacing:1px}}

*,body{margin:0;font-family:'Nunito Sans', 'Avenir Next', -apple-system, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

